/*-------------------------------------
#. EL: Logo Slider
---------------------------------------*/
.rt-logo-brand {
	.logo-box {
		&.gray {
			img {
				filter: grayscale(1);
				transition: all ease 0.21s;
				opacity: 0.4;
			}
			&:hover {
				img {
					filter: grayscale(0);
					opacity: 1;
				}

			}
		}
		&.brightness {
			img {
				filter: brightness(0)invert(1);
				transition: all ease 0.21s;
				//opacity: 0.4;
			}
			&:hover {
				img {
					opacity: 1;
				}

			}
		}
		a {
			line-height: 1;
		}

	}
}

.rt-grid-brand-logo {
	.rt-image-layout {
		.rt-image {
			a {
				img {
					filter: grayscale(100%);
					transition: all ease 0.21s;
					opacity: .5;
					&:hover {
						filter: grayscale(0);
						opacity: 1;
					}
				}
			}
		}
	}
}
