/* Back to top
======================*/
.kariez-bc-totop {
	position: fixed;
	z-index: 15;
	bottom: 46px;
	right: 40px;
	text-decoration: none;
	width: 50px;
	height: 50px;
	border: 1px solid $title_color;
	border-radius: 5px;
	background-color: $white_color;
	display: flex;
	align-items: center;
	justify-content: center;
	cursor: pointer;
	transform: translateY(120px);
	overflow: hidden;
	.progress-wrapper {
		position: absolute;
		top: 0;
		left: 0;
		right: 0;
		bottom: 0;
		overflow: hidden;
		display: block;
	}
	.progress {
		position: absolute;
		top: 3px;
		left: 3px;
		right: 3px;
		bottom: 3px;
		width: calc(100% - 6px);
		height: 0;
		background-color: $primary_color;
		border-radius: 5px;
	}
	.bc-svg {
		color: $title_color;
		font-size: 18px;

		&:before {
			width: auto;
			margin: 0;
		}
	}
	&.active {
		transform: translateY(0);
		.bc-svg {
			transform: rotate(0deg) translateX(0px);
			transition: all .3s ease;
		}
	}
}
