
/* Main Navigation
***************************/
.navigation-menu-wrap {
	@media (min-width: 1350px) {
		position: relative;
	}
	@media(max-width: 1024px) {
		padding-top: 12px;
		padding-bottom: 12px;
	}
	@media(max-width: 575px) {
		justify-content: space-between;
		gap: 12px;
		.menu-icon-wrapper {
			//margin-left: unset;
			//padding-left: 0;
			> ul {
				justify-content: center;
			}
		}
	}
}
.kariez-navigation {
	flex: 1;
	display: flex;
	align-items: center;
	.main-header-section & {
		@media (max-width: 1024px) {
			display: none;
		}
	}

	ul.kariez-navbar {
		padding: 0;
		margin: 0;
	}

	ul li {
		display: inline-block;
		position: relative;
		margin-bottom: 0;
		&:not(:last-child) {
			margin-right: 40px;
		}
	}

	.dropdown-menu li {
		margin-right: 0;
		&:not(:last-child) {
			margin-right: 0;
		}
	}

	ul li a {
		font-size: 16px;
		line-height: 22px;
		color: $title_color;
		text-decoration: none;
		padding: 36px 0;
		position: relative;
		display: inline-flex;
		align-items: center;
		justify-content: center;
		gap: 5px;
		text-transform: capitalize;
		@media (max-width: 1199px) {
			padding: 26px 15px;
		}
	}

	ul li ul li > a:after {
		content: "";
		height: 1px;
		width: 100%;
		background-color: $title_color;
		bottom: 2px;
		position: absolute;
		transform: scale3d(0, 1, 1);
		transform-origin: 100% 50%;
		transition: transform 0.4s cubic-bezier(0.7, 0, 0.2, 1), -webkit-transform 0.4s cubic-bezier(0.7, 0, 0.2, 1);
		pointer-events: none;
		left: 0;
	}

	ul li ul li:hover > a:after {
		transform: scale3d(1, 1, 1);
		transform-origin: 0% 50%;
		transition-timing-function: ease;
	}

	ul li ul li:hover > a svg {
		color: $title_color;
	}

	ul li.current-menu-item > a svg,
	ul li.current-menu-ancestor > a svg {
		color: $title_color;
	}

	ul li.current-menu-item > a,
	ul li.current-menu-ancestor > a {
		color: $title_color;
	}

	ul li a:hover {
		@extend .primaryColor;
		svg {
			color: $title_color;
		}
	}

	ul.menu > li > a:hover {
		@extend .primaryColor;
		svg {
			color: $title_color;
		}
	}

	ul.menu li.current-menu-item > a,
	ul.menu li.current > a {
		color: $title_color;
	}

	/* #. Submenu */
	ul > li > ul {

		position: absolute;
		top: 100%;
		background-color: #ffffff;
		width: 215px;
		list-style: none;
		margin: 0;
		box-shadow: 0 13px 48px 0 rgb(0 0 0 / 15%);
		visibility: hidden;
		opacity: 0;
		z-index: 9;
		transform-origin: top center;
		transform: scaleY(0) translateZ(100px);
		transition: opacity 200ms ease, visibility 200ms ease, transform 500ms ease;
		padding-left: 0;
		border-radius: 5px;
	}

	ul > li:hover > ul {
		opacity: 1;
		visibility: visible;
		transform: scaleY(1) translateZ(0px);
	}

	ul li ul li ul {
		left: 100%;
		top: 0;
		width: $dropdown_width;
	}
	ul li ul li {
		display: block;
		padding: 0 35px;
		// &:last-child {
		// 	a {
		// 		margin-bottom: 0;
		// 	}
		// }
	}

	ul li ul li a {
		position: relative;
		display: inline-block;
		color: $body_color;
		padding: 0;
		z-index: 2;
		font-size: 15px;
		font-weight: 500 !important;
		margin-bottom: 10px;
		transition: all 0.21s cubic-bezier(.3,.02,0,.6);
	}

	ul li ul > li:last-child {
		> a {
			border-bottom: none;
			margin-bottom: 0;
		}
	}

	ul li ul li:hover > a {
		color: $title_color;
		margin-left: 15px;
	}

	/*Menu hover effect*/
	ul.menu li ul.dropdown-menu li:last-child:after {
		border-bottom: none;
	}

	ul.menu li ul.dropdown-menu li a:hover {
		color: $tertiary_color;
	}

	ul.menu li ul.dropdown-menu li a:before {
		position: absolute;
		z-index: 1;
		content: "";
		@extend .tertiaryBG;
		height: 0.0625rem;
		width: 0;
		right: 0;
		left: inherit;
		bottom: 0;
		transition: all 0.7s cubic-bezier(0.645, 0.045, 0.355, 1);
	}

	ul.menu li ul.dropdown-menu li a:hover:before {
		width: 100%;
		left: 0;
		right: inherit;
	}

	> ul li.page_item_has_children > a:after,
	> ul li.menu-item-has-children > a:after {
		content: "";
		width: 10px;
		height: 10px;
		border: 1px solid $title_color;
		display: inline-block;
		transform: rotate(45deg) translate(1px, -4px);
		border-left: none;
		border-top: none;
		margin-left: 8px;
		@include transition_default();
	}

	.caret svg {
		position: relative;
		top: -1px;
		@include transition_default();
	}

	.dropdown-menu .caret {
		position: absolute;
		top: 50%;
		right: -15px;
		transform: translateY(-50%);
	}

	.dropdown-menu .caret svg {
		transform: rotate(-90deg) scale(.9);
	}
}



//Navigation offset change if it try to cross the body width
.kariez-navbar li > .dropdown-menu li {
	&.dropdown-inverse .dropdown-menu {
		left: auto;
		min-width: 195px;
		transition: all .2s;
	}

	&.dropdown-inverse:hover .dropdown-menu {
		margin-right: -5px;
	}
}
