// Project
.project-wrap-1 {
	position: relative;
	z-index: 1;
	overflow: hidden;
	&:after {
		content: "";
		height: 50%;
		width: 88%;
		background-color: $primary_color;
		position: absolute;
		bottom: 0;
		left: 0;
		z-index: -2;
		border-radius: 0 0 50px 0;
		display: none;
		@media (min-width: 1200px) {
			display: block;
		}
	}

	&:before {
		content: url(../images/project-bg.svg);
		position: absolute;
		top: 50%;
		left: 0;
		z-index: -1;
		height: 100%;
		width: 100%;
		display: none;
		@media (min-width: 1200px) {
			display: block;
		}
	}
}


.project-grid-layout-1 {
	.project-item {
		text-align: center;
		&:hover {
			.project-thumbs {
				img {
					transform: scale(1);
				}
			}
			.project-content {
				&:after {
					height: 100%;
				}
				.project-subtitle {
					color: $white_color;
				}
				.project-cat {
					background-color: $white_color;
				}
			}
		}
		.project-thumbs {
			margin-bottom: 0;
			img {
				transform: scale(1.1);
				transition: all 0.21s cubic-bezier(.3,.02,0,.6);
			}
			&:after {
				display: none;
			}
		}
		.project-content {
			background-color: $white_color;
			padding: 30px;
			margin-right: 0;
			position: absolute;
			left: 30px;
			right: 30px;
			bottom: 30px;
			z-index: 1;
			border-radius: 10px;
			&:after {
				content: "";
				position: absolute;
				z-index: -1;
				height: 0;
				width: 100%;
				bottom: 0;
				left: 0;
				border-radius: 10px;
				background-color: $primary_color;
				transition: all 0.21s cubic-bezier(.3,.02,0,.6);
			}
		}
		.project-subtitle {
			font-size: 18px;
			color: #8d8d8d;
			margin-bottom: 5px;
		}
		.project-title {
			font-size: 20px;
			font-weight: 600;
			line-height: 1.2;
			margin-bottom: 0;
			a {
				color: $title_color;
			}
		}
		.project-cat {
			display: inline-block;
			font-size: 15px;
			text-transform: capitalize;
			font-weight: 600;
			background-color: $primary_color;
			border-radius: 500px;
			padding: 8px 15px 7px;
			line-height: 1;
			margin-bottom: 10px;
			@include transition_default();
		}
		.project-excerpt {
			margin-top: 10px;
		}
	}
}

.project-grid-layout-3 {
	.project-item {
		display: block;
		text-align: left;
		@media (min-width: 992px) {
			display: flex;
			align-items: center;
			justify-content: space-between;
		}
		.project-date {
			font-size: 22px;
			font-weight: 500;
			color: $title_color;
		}
		.project-flex-item {
			@media (min-width: 992px) {
				flex: 0 0 80%;
				display: flex;
				align-items: center;
				gap: 30px;
			}
		}
		.project-content {
			margin-top: 25px;
			position: unset;
			padding: 0;
			@media (min-width: 992px) {
				margin-top: 0;
			}
			&:after {
				display: none;
			}
			.project-cat {
				display: inline-block;
				font-size: 15px;
				text-transform: capitalize;
				font-weight: 600;
				background-color: $primary_color;
				border-radius: 500px;
				padding: 8px 15px 7px;
				line-height: 1;
				margin-bottom: 10px;
				@include transition_default();
				&:hover {
					background-color: $title_color;
				}
			}
			.project-info {
				.button-3 {
					margin-top: 40px;
				}
			}
		}
	}
}

