				border-radius: 10px;
				background-color: $primary_color;
				transition: all 0.21s cubic-bezier(.3,.02,0,.6);
			}
		}
		.project-subtitle {
			font-size: 18px;
			color: #8d8d8d;
			margin-bottom: 5px;
		}
		.project-title {
			font-size: 20px;
			font-weight: 600;
