			}
		}
		.project-cat {
			display: inline-block;
			font-size: 15px;
			text-transform: capitalize;
			font-weight: 600;
			background-color: $primary_color;
			border-radius: 500px;
			padding: 8px 15px 7px;
			line-height: 1;
			margin-bottom: 10px;
			transition: all 0.3s ease-in-out;
		}
		.project-excerpt {
			margin-top: 10px;
		}
		.rt-button {
			margin-top: 20px;
		}
	}
	.rt-swiper-slider {
		position: inherit;
		.swiper-navigation {
			display: inline-flex;
			justify-content: center;
			width: 100%;
			gap: 10px;
			margin-top: 60px;
			@include transition_default();
			.swiper-button {
				&:hover {
					background-color: $title_color;
					i {
						color: $primary_color;
					}
				}
				position: relative;
				transform: translateY(0);
				border: 1px solid $title_color;
				i {
					color: $title_color;
					@include transition_default();
				}
				&:before {
					content: "";
					height: 1px;
					width: 250px;
					background-color: $title_color;
					position: absolute;
					top: 50%;
					transform: translateX(-50%);
					left: inherit;
					bottom: inherit;
					right: inherit;
					z-index: -1;
					transition: 0.4s;
					border-radius: 0;
				}
			}
			.swiper-button-prev {
				left: 0;
				opacity: 1;
				visibility: visible;
				&::before {
					left: -140px;
				}
				&.active {
					&::before {
						width: 500px;
						left: -265px;
						transition: all .6s ease-in-out;
					}
				}
			}
			.swiper-button-next {
				right: 0;
				opacity: 1;
				visibility: visible;
				&::before {
					right: -390px;
					left: auto;
				}
				&.active {
					&::before {
						width: 500px;
						right: -770px;
						transition: all .6s ease-in-out;
					}
				}
			}
		}
		.swiper-pagination {
			.swiper-pagination-bullet-active {
				background: $title_color;
			}
		}
	}
}

.project-grid-layout-5 {
	.project-item {
		text-align: left;
		.project-thumbs {
			.post-thumbnail-wrap {
				.post-thumbnail {
					position: relative;
					overflow: hidden;
					z-index: 1;
					border-radius: 10px;
					&:after {
						height: 100%;
						width: 100%;
						background-color: rgba(0, 0, 0, .5);
						content: "";
						position: absolute;
						bottom: 0;
						left: 0;
						right: 0;
						transition: all 1s ease-in-out;
					}
					img {
						width: 100%;
						transition: all 0.11s cubic-bezier(.3,.02,0,.6);
						border-radius: 10px;
					}
				}
			}
		}
		.project-content {
			background-color: transparent;
			padding: 0;
			&:after {
				display: none;
			}
			.project-number {
				color: $white_color;
				font-size: 30px;
				margin-bottom: 20px;
			}
			.project-title {
				font-size: 30px;
				a {
					color: $white_color;
					&:hover {
						color: $primary_color;
					}
				}
			}
			.project-excerpt {
				color: $white_color;
			}
		}
		.rt-button {
			.btn {
				justify-content: center;
				padding: 0;
			}
		}
	}
}



