// Working Process

.process-wrap {
	display: flex;
	flex-direction: row;
	flex-wrap: wrap;
	justify-content: center;
	@media(max-width: 1139px) {
		gap: 30px;
	}
}

.rt-working-process {
	.process-item {
		position: relative;
		text-align: center;
		@media(max-width: 1139px) {
			margin-bottom: 15px;
		}
		&.odd {
			margin-top: -40px;
			@media(max-width: 1139px) {
				margin-top: 0;
			}
		}
		&.even {
			margin-top: 70px;
			@media(max-width: 1139px) {
				margin-top: 0;
			}
		}
	}
	.rt-title {
		 font-size: 15px;
		 font-weight: 700;
		 margin-bottom: 5px;
		 line-height: 1.3;
	}
	.right {
		left: auto;
	}
}

.rt-process-layout-1 {
	position: relative;
	.rt-center-line {
		position: absolute;
		width: 100%;
		border-bottom: 1px solid #707376;
		height: 1px;
		top: 33%;
		transform: translateY(-33%);
		left: 0;
		right: 0;
		text-align: center;
		margin: auto;
		@media(max-width: 1139px) {
			display: none;
		}
	}
	.rt-step-dot {
		position: absolute;
		top: 50%;
		transform: translateY(-50%);
		width: 29px;
		height: 29px;
		border-radius: 100%;
		border: 10px solid #fff;
		background-color: $primary_color;
		transition: all 0.7s;
	}
	.has-animation {
		opacity: 0;
		visibility: hidden;
	}
	.active-animation {
		opacity: 1;
		visibility: visible;
	}

	.rt-step-dot-item1 {
		left: -10%;
	}
	.rt-step-dot-item2 {
		left: 21%;
	}
	.rt-step-dot-item3 {
		left: 39%;
	}
	.rt-step-dot-item4 {
		left: 56%;
	}
	.rt-step-dot-item5 {
		left: 75%;
	}
	.active-animation.rt-step-dot-item1 {
		left: 12%;
		transition-delay: 400ms;
	}
	.active-animation.rt-step-dot-item2 {
		left: 31%;
		transition-delay: 800ms;
	}
	.active-animation.rt-step-dot-item3 {
		left: 49%;
		transition-delay: 1200ms;
	}
	.active-animation.rt-step-dot-item4 {
		left: 66%;
		transition-delay: 1600ms;
	}
	.active-animation.rt-step-dot-item5 {
		left: 85%;
		transition-delay: 1600ms;
	}
}
.rt-process-layout-2 {
	.process-item {
		&:hover {
			.process-info-img {
				img {
					transform: scale(1.1);
				}
			}
			.rt-number {
				color: $primary_color;
				&:before {
					transform: scale(1);
				}
			}
		}
		.process-info-img {
			height: 180px;
			width: 180px;
			border-radius: 100%;
			margin: auto;
			margin-bottom: 20px;
			img {
				border-radius: 100%;
				transition: all 0.21s cubic-bezier(.3,.02,0,.6);
			}
		}
		&.odd {
			margin-top: 0;
		}
		&.even {
			margin-top: 50px;
		}
	}
	.rt-number {
		position: absolute;
		right: 80px;
		top: 60px;
		font-size: 30px;
		font-weight: 500;
		font-family: $heading_font;
		height: 80px;
		width: 80px;
		display: inline-flex;
		align-items: center;
		justify-content: center;
		color: $button_text_color;
		background-color: $primary_color;
		border-radius: 100%;
		@include transition_default();
		z-index: 1;
		transition: .6s;
		@media(max-width: 992px) {
			right: 0;
			left: 0;
			margin: auto;
		}
		&:before {
			content: "";
			height: 100%;
			width: 100%;
			background-color: $title_color;
			position: absolute;
			top: 0;
			left: 0;
			right: 0;
			bottom: 0;
			border-radius: 100%;
			transform: scale(0);
			transition: .3s;
			z-index: -1;
		}
	}
	.process-content {
		.rt-title {
			font-size: 24px;
			font-weight: 600;
		}
	}
}

