.rt-marquee-slider {
	position: relative;
}
.rt-marquee-slider .rt-marquee {
	width: 100%;
	overflow: hidden;
	white-space: nowrap;
	padding: 20px;
	line-height: 1;
}
.rt-marquee-slider .rt-marquee.marquee-left .rt-marquee-item {
	display: inline-block;
	will-change: transform;
	animation: marquee_left 40s linear infinite;
}
.rt-marquee-slider .rt-marquee.marquee-right .rt-marquee-item {
	display: inline-block;
	will-change: transform;
	animation: marquee_right 40s linear infinite;
}
.rt-marquee-slider .rt-marquee:hover .rt-marquee-item {
	animation-play-state: paused;
}
.rt-marquee-slider .rt-marquee-item .entry-title {
	display: inline-flex;
	align-items: center;
	font-size: 30px;
	line-height: 1.4;
	font-weight: 600;
	position: relative;
	margin-bottom: 0;

	a {
		color: $title_color;
	}
	&:hover {
		a {
			color: $white_color;
		}
		.icon-holder {
			svg {
				path {
					fill: $white_color;
				}
			}
		}
	}
	.icon-holder {
		display: flex;
		align-items: center;
		justify-content: center;
		width: 80px;
		text-align: center;
		svg {
			path {
				fill: $title_color;
				transition: all .3s ease-in-out;
			}
		}
	}
}



