// Info Box Style

.rt-info-box {
	.info-box {
		position: relative;
		background-size: cover;
		@include transition_default;
		z-index: 1;

		&:hover {
			p {
				color: $title_color;
			}
		}
		.info-icon {
			display: inline-flex;
			align-items: center;
			justify-content: center;
			font-size: 40px;
			margin-bottom: 15px;
			@include transition_default();
			i, svg, img {
				display: flex;
				@include transition_default();
				color: $title_color;
			}
		}
		&:hover {
			.info-icon {
				i, svg, img {
					transform: scale(1.05);
				}
			}
		}
	}
	.info-icon-holder {
		position: relative;
		margin-bottom: 18px;
		line-height: 0;
		a {
			display: inline-block;
		}
	}
	.content-holder {
		p {
			margin-bottom: 0;
			line-height: 30px;
		}
	}
	.rt-info-image {
		position: relative;
		overflow: hidden;
		margin-top: 24px;
		line-height: 0;
	}
	.rt-button {
		margin-top: 24px;
	}
	.info-title {
		font-size: 24px;
		line-height: 30px;
		font-weight: 600;
		a {
			color: $title_color;
		}
		@media(max-width: 767px) {
			br {
				display: none;
			}
		}
	}
	.feature-list {
		@include list_default;
		margin-top: 18px;
		li {
			font-size: 17px;
			font-weight: 600;
			margin-bottom: 10px;
			color: $title_color;
			.icon {
				display: inline-flex;
				align-items: center;
				justify-content: center;
				font-size: 10px;
				width: 24px;
				height: 24px;
				margin-right: 9px;
				border-radius: 100%;
				color: $white_color;
				background-color: $primary_color;
			}
		}
	}
