// style 1
.rt-info-layout-1 {
	.info-box {
		background-color: $secondary_color;
		padding: 70px 45px 60px;
		overflow: hidden;
		z-index: 1;
		position: relative;

		&:hover {
			p {
				color: $title_color;
			}
		}
	}
	.info-icon-holder {
		margin-bottom: 15px;
	}
	.info-title {
		margin-bottom: 0;
	}
}



// style 2
.rt-info-layout-2 {
	.info-box {
		padding: 45px 20px 35px;
		background-color: $white_color;
		display: flex;
		column-gap: 20px;
		box-shadow: 0px 0px 50px rgba(0, 0, 0, 0.04);
		.info-icon {
			position: relative;
			z-index: 0;
		}
		@media(max-width: 480px) {
			display: block;
		}
	}
}
// style 3
.rt-info-layout-3 {
	.info-box {
		padding: 50px;
		text-align: center;
		position: relative;
		z-index: 1;
		transition: all .65s cubic-bezier(0.23,1,0.320,1);
		background-color: $white_color;
		&:hover {
			background-color: $primary_color;
			box-shadow: 7px 7px 0 0 rgba(0, 0, 0, 0.2);
			transform: translateY(5px);
			.info-icon {
				color: $title_color;
				i {
					transform: scale(1.1);
				}
			}
			p {
				color: $title_color;
			}
		}
	}
}


.rt-info-slider {
	.rt-swiper-slider {
		.swiper-navigation {
			.swiper-button {
				height: 60px;
				width: 60px;
				background-color: $title_color;
				border: 0;
				position: absolute;
				transform: translateY(-50%);
				top: 50%;
				border-radius: 10px;
				&:before {
					display: none;
				}
				&:hover {
					background-color: $title_color;
					i {
						color: $primary_color;
					}
				}

				i {
					font-size: 22px;
					color: $primary_color;
					@include transition_default();
					&:before {
						width: auto;
						margin: 0;
					}
				}
				&.active {
					background-color: $primary_color;
					i {
						color: $title_color;
					}
				}
			}
			.swiper-button-prev {
				left: 0;
				opacity: 1;
				visibility: visible;
			}
			.swiper-button-next {
				right: 0;
				opacity: 1;
				visibility: visible;

			}
		}
		.swiper-pagination {
			.swiper-pagination-bullet {
				background: $title_color;
			}
			.swiper-pagination-bullet-active {
				background: $primary_color;
			}
		}
	}
	.rt-info-slider-box {
		background-color: $white_color;
		//box-shadow: 0px 0px 50px rgba(0, 0, 0, 0.04);
		padding: 60px 38px 50px;
		position: relative;
		z-index: 1;
		overflow: hidden;
		&:hover {
			&:after {
				transform: translateY(0);
			}
			p {
				color: $title_color;
			}
		}
		&:after {
			content: "";
			height: 100%;
			width: 100%;
			background-color: $primary_color;
			position: absolute;
			border-radius: 10px;
			top: 0;
			right: 0;
			left: 0;
			bottom: 0;
			z-index: -1;
			transform: translateY(-100%);
			transition: all .4s ease;
		}
		.info-slider-box {

			.rt-icon {
				font-size: 50px;
				color: $title_color;
				margin-bottom: 20px;
				i {
					&:before {
						margin: 0;
						width: auto;
					}
				}
			}
			.info-title {
				font-size: 28px;
				font-weight: 600;
				line-height: 30px;
				margin-bottom: 25px;
			}
			p {
				transition: all .3s ease-in-out;
			}
		}
	}
}

.info-btn {
	font-weight: 600;
	text-transform: capitalize;
	position: relative;
	&:hover {
		color: $title_color;
		&:before {
			width: 100%;
		}
	}
	&:before {
		content: "";
		height: 1px;
		width: 0;
		background-color: $title_color;
		position: absolute;
		left: 0;
		bottom: 0;
		transition: width .4s ease;
	}
}









