/* Mega-menu CSS
***************************/
.mega-menu-wrap {
	.e-con-inner {
		position: relative;
	}
	.elementor-element,
	.elementor-widget-container {
		position: unset;
	}
}
.kariez-navigation {
	ul.menu li.mega-menu ul.dropdown-menu li a:before,
	ul.menu li.mega-menu ul.dropdown-menu li:after,
	ul.menu li.mega-menu ul.dropdown-menu li.menu-item-has-children:before {
		display: none;
	}

	ul.menu li.mega-menu ul.dropdown-menu li a {
		padding: 0 0 8px;
	}

	ul.menu li.mega-menu .dropdown-menu .dropdown-toggle {
		padding-bottom: 0 !important;
	}

	ul li.mega-menu {
		position: static;
	}

	ul li.mega-menu:hover ul {
		opacity: 1;
		visibility: visible;
	}

	ul li.mega-menu > ul.dropdown-menu {
		justify-content: flex-start;
		display: flex;
		background-color: $body_bg_color;
		width: 100%;
		position: absolute;
		left: 0;
		right: 0;
		margin: auto;
	}

	ul li > ul.dropdown-menu {
		transform-origin: top center;
		transform: scaleY(0) translateZ(100px);
		transition: opacity 200ms ease, visibility 200ms ease, transform 500ms ease;
		pointer-events: none;
		padding-top: 30px;
		padding-bottom: 30px;
	}

	ul > li:hover > ul.dropdown-menu {
		//transform: translateY(0);
		transform: scaleY(1) translateZ(0px);
		pointer-events: all;
	}


	/*start mega menu two column*/
	ul li.mega-menu.two-column {
		position: relative;
	}

	ul li.mega-menu.two-column > ul.dropdown-menu {
		background-color: $white_color !important;
		width: 520px !important;
	}

	/*start mega menu three column*/
	ul li.mega-menu.two-column {
		position: relative;
	}

	ul li.mega-menu.three-column > ul.dropdown-menu {
		background-color: $white_color !important;
		width: 780px !important;
	}


	/*end mega menu three column*/
	ul li.mega-menu {
		> ul.dropdown-menu {
			> li {
				display: inline-block;
				width: 100%;
				border: none;
				> ul.dropdown-menu {
					transform: unset;
				}
				ul {
					position: relative;
					width: inherit;
					left: inherit;
					box-shadow: none;
					border-top: none;
					padding: 0;
				}
				.sub-menu {
					li {
						padding: 0;
					}
				}
				> a {
					font-weight: 600 !important;
					font-size: 19px;
					margin-bottom: 10px;
					padding-left: 15px;
					//border-bottom: 1px solid $title_color;
					position: relative;
					text-transform: uppercase;
					color: $title_color;
					&:before {
						content: "";
						height: 2px;
						width: 10px;
						background-color: $title_color;
						position: absolute;
						top: 10px;
						left: 0;
						visibility: visible;
						opacity: 1;
					}
					&:hover {
						margin-left: 0;
					}
					&:after {
						display: none;
					}
				}
			}
		}

	}

	ul li.mega-menu .dropdown-menu .dropdown-submenu:hover .dropdown-toggle  {
		color: $title_color;
		margin-left: 0;
	}

	ul li.mega-menu.hide-header > ul.dropdown-menu > li > a {
		display: none;
	}

	ul li.mega-menu > ul .caret {
		display: none !important;
	}

	ul li.mega-menu.mega-menu-col-2 > ul {
		width: 50%;
	}

	ul li.mega-menu.mega-menu-col-3 > ul {
		width: 65%;

	}
}


