/* Transparent Header */
.has-trheader {
	&.has-menu-shadow .site-header {
		&::before {
			content: "";
			background: rgba(0, 0, 0, 0);
			background: linear-gradient(to bottom, rgb(0 0 0 / 68%) 0%, rgba(0, 0, 0, 0) 100%);
			top: 0;
			position: absolute;
			width: 100%;
			height: 220px;
			z-index: -1;
			pointer-events: none;
			@media (max-width: 767px) {
				display: none;
			}
		}
	}
	.site-header {
		.kariez-navigation ul li a svg {
			//opacity: 0.7;
		}
	}
}

.tr-header-light {
	.light-logo {
		display: block;
	}
	.dark-logo, .rt-mobile-logo {
		display: none;
	}
	.kariez-navigation .menu-main-menu-container {
		> ul {
			> li {
				> a {
					color: $white_color;

					svg {
						color: #fff;
					}
				}
			}
		}
	}

	.ham-burger {
		.btn-hamburger {
			span {
				background-color: $white_color;
			}
		}
		.menu-label {
			color: $white_color;
		}
	}

	.menu-icon-wrapper {
		.menu-search-bar {
			&:hover {
				i {
					color: $title_color;
				}
			}
			i {
				color: $white_color;
			}
		}
		a svg {
			color: $white_color;
		}

		.ham_burger {
			.line {
				stroke: $white_color;
			}
		}
		.rt-phone-2 {
			.phone-box {
				.content {
					.phone-label {
						color: #E9E5E5;
					}
					.phone-number {
						color: #fff;
						&:hover {
							color: $primary_color;
						}
					}
				}
			}
		}
		.has-separator li:not(:last-child):after {
			background-color: $white_color;
		}
		.rt-button {
			.button-3 {
				border: 1px solid #fff;
				color: $white_color;
				&:hover {
					border-color: $primary_color;
				}
			}
		}
	}
}

