.layout-left-sidebar {
	.kariez-widget-area {
		@media (min-width: 1140px) {
			padding-right: 40px;
		}
	}
}
.layout-right-sidebar {
	.kariez-widget-area {
		@media (min-width: 1140px) {
			padding-left: 40px;
		}
	}
}
@media(max-width: 1140px) {
	.layout-right-sidebar {
		.kariez-widget-area {
			padding-top: 60px;
		}
	}
	.layout-left-sidebar {
		.kariez-widget-area {
			padding-bottom: 60px;
		}
	}
}
@media(min-width: 1025px) {
	body .sidebar-sticky {
		align-self: flex-start;
		position: sticky;
		top: 32px;
	}
	body .sidebar-sticky-100 {
		align-self: flex-start;
		position: sticky;
		top: 100px;
		transition: all 300ms linear;
	}
	body .sidebar-sticky-200 {
		align-self: flex-start;
		position: sticky;
		top: 200px;
	}
}
.kariez-widget-area {
	.widget:last-child {
		margin-bottom: 0;
	}
	ul {
		list-style: none;
		margin: 0;
		padding: 0;
		> li {
			position: relative;
			margin-bottom: 5px;
			font-size: 16px;
			&:last-child {
				margin-bottom: 0;
			}
			> a {
				position: relative;
			}
		}
		&.sub-menu {
			margin-top: 5px;
		}
	}
	.widget-title, .wp-block-heading {
		font-size: 30px;
		font-weight: 600;
		margin-bottom: 25px;
	}

	.rt-sidebar, .rt-single-sidebar {
		border: 1px solid #E5E5E5;
		width: 100%;
		max-width: 100%;
		margin-bottom: 30px;
		padding: 33px;
		border-radius: 10px;
		ul.children {
			margin-top: 10px;
		}
	}

	.wp-block-categories,
	.wp-block-archives,
	.wp-block-page-list,
	.widget_archive ul,
	.widget_categories ul {
		> li {
			width: 100%;
			padding-bottom: 15px;
			margin-bottom: 15px;
			position: relative;
			border-bottom: 1px solid #dcdcdc;
			&:after {
				content: "\e834";
				position: absolute;
				font-family: rt-fontello;
				right: 12px;
				top: 0;
				color: $title_color;
				transition: all 320ms ease-in-out;
			}
			&:before {
				content: "";
				height: 1px;
				width: 0;
				border-bottom: 1px solid $title_color;
				position: absolute;
				left: 100%;
				bottom: -1px;
				transition: all 0.5s ease-in-out;
				visibility: hidden;
				opacity: 0;
			}
			> a {
				font-size: 18px;
				font-weight: 500;
				color: $title_color;
				transition: all 0.4s ease-in-out;
				position: relative;
				&:hover {
					color: $primary_color;
				}
			}
			&:hover {
				&:before {
					width: 100%;
					visibility: visible;
					opacity: 1;
					transition: width 0.5s ease;
					left: 0%;
				}
			}
			&:last-child {
				margin-bottom: 0;
				border-bottom: 0;
				padding-bottom: 0;
				&:before {
					display: none;
				}
			}
		}
	}

	.wp-block-categories.category-layout-2,
	.wp-block-archives.category-layout-2,
	.widget_archive.category-layout-2 ul,
	.widget_categories.category-layout-2 ul {
		li {
			a {
				color: $meta_color;
				&:before {
					position: absolute;
					content: '';
					left: 0;
					top: 5px;
					width: 8px;
					height: 8px;
					opacity: 1;
					visibility: visible;
					border-radius: 50%;
					background-color: $border_color;
					@include transition_default();
				}
				&:hover {
					padding-left: 22px;
					color: $primary_color;
					&:before {
						background-color: $primary_color;
					}
				}
			}
		}
	}

	.widget_nav_menu,
	.widget_pages,
	.widget_meta {
		ul {
			> li {
				width: 100%;
				padding-bottom: 15px;
				margin-bottom: 15px;
				position: relative;
				border-bottom: 1px solid #dcdcdc;
				&:after {
					content: "\e834";
					position: absolute;
					font-family: rt-fontello;
					right: 12px;
					top: 0;
					color: $title_color;
					transition: all 320ms ease-in-out;
				}
				&:before {
					content: "";
					height: 1px;
					width: 0;
					border-bottom: 1px solid $title_color;
					position: absolute;
					left: 100%;
					bottom: -1px;
					transition: all 0.5s ease-in-out;
					visibility: hidden;
					opacity: 0;
				}
				> a {
					font-size: 18px;
					font-weight: 500;
					color: $title_color;
					transition: all 0.4s ease-in-out;
					position: relative;
					&:hover {
						color: $primary_color;
					}
				}
				&:hover {
					&:before {
						width: 100%;
						visibility: visible;
						opacity: 1;
						transition: width 0.5s ease;
						left: 0%;
					}
				}
				&:last-child {
					margin-bottom: 0;
					border-bottom: 0;
					padding-bottom: 0;
					&:before {
						display: none;
					}
				}
			}
		}
	}

	.wp-block-tag-cloud, .tagcloud {
		display: flex;
		flex-wrap: wrap;
		gap: 10px;
		margin-bottom: 0;
		a {
			color: $title_color;
			background-color: transparent;
			border: 1px solid $border_color;
			border-radius: 5px;
			display: inline-block;
			font-size: 14px !important;
			font-weight: 500;
			margin: 0;
			padding: 10px;
			line-height: 1;
			&:hover {
				background-color: $primary_color;
				color: $title_color;
				border-color: $primary_color;
			}
		}
	}

	.widget_media_image {
		//border: 0;
		//padding: 0;
		border-radius: 10px;
		.wp-block-image {
			margin-bottom: 0 !important;
			img {
				border-radius: 10px;
			}
		}
	}
	.widget_text {
		.textwidget {
			img {
				border-radius: 10px;
			}
		}
	}


	.rt-blog-post {
		margin-bottom: 30px;
		&:last-child {
			margin-bottom: 0;
		}
		.post-thumbnail {
			overflow: hidden;
			border-radius: 6px;
			margin-bottom: 0;
			img {
				object-fit: cover;
				object-position: center;
			}
		}
		&:hover {
			img {
				transform: scale(1.09);
			}
		}
		ul {
			li {
				font-size: 15px;
				border: none;
				margin: 0;
				padding: 0;
			}
		}
		.rt-post-meta {
			padding-top: 20px;
		}
		.entry-title {
			font-size: 16px;
			font-weight: 600;
			margin-bottom: 0;
			line-height: 1.4;
			text-overflow: ellipsis;
			display: -webkit-box;
			-webkit-line-clamp: 2;
			-webkit-box-orient: vertical;
			overflow: hidden;
			a {
				color: $title_color;
				&:hover {
					@extend .primaryColor;
				}
			}
		}
		.entry-content {
			padding-top: 12px;
		}
		.post-thumbnail-wrap .posted-on {
			display: none;
		}
	}
	.blog-list-style {
		.rt-post-meta {
			padding-top: 0;
		}
		.article-inner-wrapper {
			display: flex;
			align-items: center;
			column-gap: 15px;
		}
		.post-thumbnail-wrap {
			width: 80px;
		}
		.entry-wrapper {
			flex: 1;
		}
	}
}
