// Video Icon Block

.rt-video-icon {
	&.icon-style1 {
		.video-icon {
			position: relative;
			display: inline-flex;
			align-items: center;
			&:hover {
				.icon-box {
					.video-popup-icon {
						background-color: $primary_color;
						border-color: $primary_color;
						i {
							color: $title_color;
						}
					}
				}
			}
		}
		.icon-box {
			.video-popup-icon {
				height: 60px;
				width: 60px;
				display: inline-flex;
				align-items: center;
				justify-content: center;
				border: 1px solid #969696;
				border-radius: 100%;
				transition: all .3s ease-in-out;
				background-color: transparent;
				i {
					color: #969696;
					font-size: 18px;
					transition: all .3s ease-in-out;
				}

			}
		}
		.button-text {
			margin-left: 15px;
			display: inline-block;
			font-size: 15px;
			color: $title_color;
			font-weight: 600;
			text-transform: capitalize;
			transition: all .3s ease-in-out;
			&:hover {
				color: $primary_color;
			}
		}
	}
}

.rt-video-icon.icon-style2 {
	position: relative;
	background-size: cover;
	background-position: center;
	background-repeat: no-repeat;

	.video-icon {
		position: absolute;
		z-index: 1;
		left: 50%;
		top: 50%;
		transform: translateY(-50%) translateX(-50%);
		right: auto;
		@media (min-width: 1200px) {
			right: -50px;
			top: 40%;
			transform: translateY(-60%);
			left: auto;
		}
	}
	.icon-box {
		position: relative;
		z-index: 1;

	}
	.video-popup-icon {
		width: 110px;
		height: 110px;
		border-radius: 100%;
		background-color: $primary_color;
		text-align: center;
		position: relative;
		display: flex;
		justify-content: center;
		align-items: center;
		&:hover {
			&::before {
				transform: scale(1);
			}
			i {
				color: $white_color;
			}

		}
		&::before {
			content: "";
			height: 100%;
			width: 100%;
			transform: scale(0);
			background-color: $title_color;
			position: absolute;
			top: 0;
			left: 0;
			bottom: 0;
			right: 0;
			transition: 0.4s;
			border-radius: 100%;
		}
		i {
			font-size: 40px;
			color: $title_color;
			position: relative;
			z-index: 1;
		}
	}
	.about-shape {
		height: 220px;
		width: 220px;
		background-color: $white_color;
		border-radius: 100%;
		display: inline-flex;
		align-items: center;
		justify-content: center;
		transition: all 0.3s ease-in-out;
		position: absolute;
		top: 50%;
		left: 50%;
		transform: translateY(-50%) translateX(-50%);
		z-index: -1;
		.shape {
			animation-duration: 15s;
		}
	}
}

.icon-style3 {
	background-attachment: fixed;
	background-repeat: no-repeat;
	background-size: cover;
	.video-icon {
		position: absolute;
		z-index: 1;
		left: 50%;
		top: 50%;
		transform: translateY(-50%) translateX(-50%);
		right: auto;

		.icon-box {
			.video-popup-icon {
				display: inline-flex;
				align-items: center;
				justify-content: center;
				i {
					font-size: 40px;
				}
			}
		}
	}
}

.video-text-wrap {
	.elementor-icon-box-icon {
		.elementor-icon {
			height: 60px;
			width: 60px;
			display: inline-block;
			text-align: center;
			background-color: $white_color;
			border-radius: 100%;
			line-height: 22px;
			i {
				color: $title_color;
				width: auto;
				height: auto;
				position: relative;
				display: inline-block;
				&:before {
					width: auto;
					line-height: inherit;
					margin: 0;
				}
			}
		}
	}
}


.rt-flying-video:before {
	position: absolute;
	top: 0;
	left: 0;
	bottom: 0;
	width: 1440px;
	height: 100%;
	clip-path: polygon(0 0, 100% 0, 80% 100%, 0% 100%);
	background: url(../images/hero-bg.svg);
	background-repeat: no-repeat;
	background-size: cover;
	z-index: 2;
	@media (max-width: 2450px) {
		width: 1200px !important;
	}
	@media (max-width: 2000px) {
		width: 1100px;
	}
}

