.location-map {
	position: relative;
	height: 100%;
	.map-image {
		background-color: $white_color;
		box-shadow: 0px 0px 50px rgba(0, 0, 0, 0.04);
		border-radius: 10px;
		text-align: right;
		img {
			opacity: .5;
		}
	}
}



.locations-wrapper {
	position: absolute;
	&:hover {
		.location-content {
			opacity: 1;
			z-index: 1;
			transform: translateX(-50%) translateY(0);
		}
	}
}



.location-content {
	min-width: 250px;
	bottom: -85px;
	left: 50%;
	transform: translateX(-50%) translateY(20px);
	position: absolute;
	border-radius: 500px;
	background-color: $white_color;
	box-shadow: 0px 0px 50px rgba(0, 0, 0, 0.1);
	color: $title_color;
	padding: 15px 25px;
	font-size: 15px;
	font-weight: 500;
	line-height: 20px;
	text-align: center;
	opacity: 0;
	z-index: -2;
	transition: all 0.3s ease-out;
	&:before {
		content: "";
		position: absolute;
		top: -7px;
		left: 50%;
		transform: translateX(-50%);
		width: 0;
		height: 0;
		border-left: 7px solid transparent;
		border-right: 7px solid transparent;
		border-bottom: 7px solid #fff;
	}
}

.location-point {
	width: 15px;
	height: 15px;
	background-color: $title_color;
	border-radius: 50%;
	position: relative;
	.blinking-1 {
		position: absolute;
		left: 0;
		top: 0;
		height: 100%;
		width: 100%;
		border-radius: inherit;
		color: inherit;
		box-shadow: 0 0 0 15px rgba(255, 204, 0, 1);
		animation: pulsekm 3s infinite cubic-bezier(0.4, 0, 1, 1) both;
	}
}



