


 //Footer builder

.elementor-widget-rt-site-logo {
	.site-branding {
		.rt-mobile-logo, .rt-site-logo {
			display: none !important;
		}
		&.dark {
			.dark-logo {
				display: block !important;
			}
		}
		&.light {
			.light-logo {
				display: block !important;
			}
		}
	}
}

.kariez-footer-builder {
	.kariez-navigation {
		ul {
			li {
				margin-right: 0;
				position: relative;
				&:after {
					background-color: $border_color;
					position: absolute;
					content: "";
					top: 18px;
					left: 0;
					height: 5px;
					width: 5px;
					border-radius: 100%;

				}
				a {
					position: relative;
					margin-left: 15px;
					&:hover {
						&:before {
							transform: scale3d(1, 1, 1);
							transform-origin: 0% 50%;
							transition-timing-function: ease;
						}
					}
					&:before {
						content: "";
						height: 1px;
						width: 100%;
						background-color: $primary_color;
						bottom: 10px;
						position: absolute;
						transform: scale3d(0, 1, 1);
						transform-origin: 100% 50%;
						transition: transform 0.4s cubic-bezier(0.7, 0, 0.2, 1);
						pointer-events: none;
					}
				}
			}
		}
	}
}

 .rt-copyright-menu {
	 .kariez-navbar {
		 li {
			 &:after {
				 display: none;
			 }
			 &:last-child {
				 a {
					 &:after {
						 display: none;
					 }
				 }
			 }
			 &:not(:last-child) {
				 margin-right: 30px;
			 }
			 a {
				 position: relative;
				 font-weight: 500 !important;
				 margin-left: 0 !important;
				 &:after {
					 content: "";
					 height: 20px;
					 width: 1.5px;
					 background-color: #8B8B8B;
					 position: absolute;
					 top: 2px;
					 transform: rotate(15deg);
					 right: -20px;
				 }
				 &:before {
					 display: none;
				 }
			 }
		 }
	 }
 }


