.ham-burger {
	.menu-bar {
		position: relative;
		display: flex;
		align-items: center;
		justify-content: center;
		background-color: transparent;
		column-gap: 12px;
		padding: 0;
		z-index: 1;
		height: 50px;
		width: 50px;
		@include transition_default();
		&:hover {
			&:before {
				transform: scale(1);
			}
		}
		&:before {
			content: "";
			height: 100%;
			width: 100%;
			background-color: $primary_color;
			position: absolute;
			top: 0;
			left: 0;
			right: 0;
			bottom: 0;
			transform: scale(0);
			transition: .4s;
			z-index: -1;
		}
	}
	.menu-label {
		color: $title_color;
		font-weight: 600;
	}
	.btn-hamburger {
		span {
			display: block;
			width: 30px;
			height: 2px;
			margin: auto;
			margin-bottom: 10px;
			background-color: $title_color;
			border-radius: 12px;
			@include transition_default();
			&:nth-child(2) {
				width: 40px;
			}
			&:nth-child(3) {
				margin-bottom: 0;
			}
		}
	}

	&:hover {
		.btn-hamburger {
			span {
				width: 40px;
			}
		}
	}
}
