// Project Archive

.rt-project-default {
	.project-item {
		text-align: center;
		.project-thumbs {
			position: relative;
			overflow: hidden;
			z-index: 1;
			border-radius: 10px;
			img {
				width: 100%;
				transition: all 0.21s cubic-bezier(.3,.02,0,.6);
				border-radius: 10px;
			}
			&.is-blend {
				img {
					filter: grayscale(100%);
				}
			}
			&:hover {
				&.is-blend {
					img {
						filter: grayscale(0);
					}
				}
				.project-thumbs {
					img {
						transform: scale(1.1);
					}
				}
			}
			&:after {
				height: 40%;
				width: 100%;
				background-color: #000;
				filter: blur(100px);
				content: "";
				position: absolute;
				bottom: 0;
				left: 0;
				right: 0;
				transition: all 1s ease-in-out;
			}
		}
		&:hover {
			.project-thumbs {
				img {
					transform: scale(1);
				}
			}
			.project-content {
				&:after {
					height: 100%;
				}
				.project-subtitle {
					color: $white_color;
				}
				.project-cat {
					background-color: $white_color;
				}
			}
		}
		.project-thumbs {
			margin-bottom: 0;
			img {
				transform: scale(1.05);
			}
			&:after {
				display: none;
			}
		}
		.project-content {
			background-color: $white_color;
			padding: 30px;
			margin-right: 0;
			position: absolute;
			left: 30px;
			right: 30px;
			bottom: 30px;
			z-index: 1;
			border-radius: 10px;
			&:after {
				content: "";
				position: absolute;
				z-index: -1;
				height: 0;
				width: 100%;
				bottom: 0;
				left: 0;
				border-radius: 10px;
				background-color: $primary_color;
				transition: all 0.3s ease-in-out;
			}
		}
		.project-subtitle {
			font-size: 18px;
			color: #8d8d8d;
			margin-bottom: 5px;
		}
		.project-title {
			font-size: 20px;
			font-weight: 600;
			line-height: 1.2;
			margin-bottom: 0;
			a {
				color: $title_color;
			}
		}
		.project-cat {
			display: inline-block;
			font-size: 15px;
			text-transform: capitalize;
			font-weight: 600;
			background-color: $primary_color;
			border-radius: 500px;
			padding: 8px 15px 7px;
			line-height: 1;
			margin-bottom: 10px;
			@include transition_default();
		}
		.project-excerpt {
			margin-top: 10px;
		}
		.rt-button {
			margin-top: 20px;
		}
	}
}

.rt-project-multi-layout-2 {
	.project-item {
		position: relative;
		text-align: left;
		@include transition_default;
		.project-thumbs {
			.post-thumbnail-wrap {
				.post-thumbnail {
					position: relative;
					overflow: hidden;
					z-index: 1;
					border-radius: 10px;
					&:after {
						height: 50%;
						width: 100%;
						background-color: #000;
						filter: blur(100px);
						content: "";
						position: absolute;
						bottom: 0;
						left: 0;
						right: 0;
						transition: all 1s ease-in-out;
						border-radius: 10px;
					}
					img {
						width: 100%;
						transition: all 0.21s cubic-bezier(.3,.02,0,.6);
						border-radius: 10px;
					}
				}
			}
			.project-content {
				position: absolute;
				bottom: 0;
				left: 0;
				z-index: 1;
				padding: 30px;
				width: 100%;
				background-color: transparent;
				//@media(min-width: 1200px) {
				//	padding: 0 120px 50px 60px;
				//}
				&:after {
					display: none;
				}
			}
			.project-title {
				font-size: 30px;
				line-height: 1.4;
				font-weight: 700;
				position: relative;
				padding-bottom: 14px;
				margin-bottom: 16px;

				a {
					color: $white_color;
					-webkit-background-clip: text;
					-webkit-text-fill-color: #fff0;
					background-image: linear-gradient(to right, $primary_color, $primary_color 50%, $secondary_color 50%);
					background-size: 200% 100%;
					background-position: 100%;
					transition: all 0.3s cubic-bezier(0,0,.23,1);
					word-break: break-word;
				}
				&:before {
					content: "";
					height: 1px;
					background-color: $primary_color;
					position: absolute;
					transition: width .3s;
					bottom: 0;
					left: 0;
					width: 0;
					opacity: 0;
					display: block;

				}
				&:after {
					height: 1px;
					width: 100%;
					background-color: #d9d9d9;
					content: "";
					position: absolute;
					bottom: 0;
					left: 0;
					opacity: 1;
					transition: all 0.5s ease-in-out;
					display: block;
				}
				a {
					color: $white_color;
				}
			}
			.project-cat {
				font-size: 15px;
				text-transform: uppercase;
				transition: all 1s ease-in-out;
				letter-spacing: 4.5px;
				display: inline-block;
				background-color: transparent;
				padding: 0;
				a {
					color: $white_color;
				}
			}
			.project-excerpt {
				margin-top: 12px;
				color: $white_color;
			}
			.rt-button {
				margin-top: 20px;
			}
			&.is-blend {
				img {
					filter: grayscale(100%);
				}
			}
			&:hover {
				&.is-blend {
					img {
						filter: grayscale(0);
					}
				}
				.project-thumbs {
					img {
						transform: scale(1.05);
					}
				}
			}
		}
		&:hover {
			.project-thumbs {
				img {
					transform: scale(1.1);
					filter: grayscale(100%);
				}
				.project-content {
					.project-title {
						a {
							background-position: 0%;

						}
						&:before {
							width: 100%;
							opacity: 1;
						}
						&:after {
							opacity: 0;
							transition: all 0.5s ease-in-out;
						}
					}
					.project-cat {
						background-color: transparent;
						a {
							color: $primary_color;
						}
					}
				}
			}
		}
	}
}



// Project Single
.project-single {
	@media(min-width: 1140px) {
		.project-item-wrap {
			display: flex;
			gap: 24px;
			justify-content: space-between;
		}
		.project-item-content {
			flex: 1;
		}
		.project-content-info {
			max-width: 33.3333333333%;
			flex: 0 0 33.3333333333%;
		}
	}
	@media(max-width: 1139px) {
		.project-item-content {
			margin-bottom: 48px;
		}
	}
	.post-thumbnail-wrap {
		img {
			border-radius: 10px;
		}
	}
	.project-item-content {
		.wp-block-image {
			img {
				border-radius: 12px;
			}
		}
		 ul {
			list-style: none;
			margin: 0 0 10px;
			padding: 0;
			li {
				position: relative;
				padding-left: 32px;
				font-size: 18px;
				color: $title_color;
				font-weight: 500;
				+ li {
					margin-top: 10px;
				}
				&:before {
					position: absolute;
					left: 0;
					top: 2px;
					font-size: 18px;
					content: "\e839";
					font-family: "rt-fontello";
					transition: all ease 0.31s;
					color: $primary_color;
				}
			}
		}
	}
	.project-information {
		@media (min-width: 1140px) {
			padding-right: 60px;
		}
		@media (max-width: 1139px) {
			margin-bottom: 40px;
		}
		ul {
			list-style: none;
			margin: 0;
			padding: 0;
		}
		.info-title {
			font-size: 30px;
			line-height: 1.4;
			font-weight: 600;
		}
		.info-list {
			> li {
				border-bottom: 1px solid $border_color;
				margin-bottom: 10px;
				padding-bottom: 10px;
				&:last-child {
					border-bottom: none;
					margin-bottom: 0;
					padding-bottom: 0;
				}
			}
			a {
				color: $body_color;
				&:hover {
					color: $primary_color;
				}
			}
			label {
				color: $title_color;
				font-size: 15px;
				width: 110px;
				display: inline-block;
			}
		}
		ul.rating {
			display: inline-flex;
			column-gap: 5px;
			li.star-rate {
				i {
					color: $tertiary_color;
				}
			}
		}
	}
	.project-content {
		margin-top: 30px;
		.entry-title {
			font-size: 30px;
			margin-bottom: 20px;
			line-height: 1.2;
			font-weight: 600;
		}
	}
}
// popup project
.elementor-lightbox {
	.elementor-lightbox-image {
		border-radius: 12px;
	}
	.elementor-swiper-button {
		> i {
			background-color: $white_color;
			color: $button_text_color;
			border-radius: 50%;
			display: flex;
			align-items: center;
			justify-content: center;
			width: 44px;
			height: 44px;
		}
	}
}
// Related project
.rt-related-project {
	margin-top: 60px;

	.related-title {
		font-size: 40px;
		line-height: 52px;
		font-weight: 600;
		margin-bottom: 36px;
	}
}
