@-webkit-keyframes upsdown {
	0% {
		transform: translateY(-100px);
		visibility: hidden;
		opacity: 0;
	}

	100% {
		transform: translateY(0);
		visibility: visible;
		opacity: 1;
	}
}

@keyframes upsdown {
	0% {
		transform: translateY(-100px);
		visibility: hidden;
		opacity: 0;
	}

	100% {
		transform: translateY(0);
		visibility: visible;
		opacity: 1;
	}
}

@-webkit-keyframes toptobottom {
	49% {
		transform: translateY(100%);
	}

	50% {
		opacity: 0;
		transform: translateY(-100%);
	}

	51% {
		opacity: 1;
	}
}

@keyframes toptobottom {
	49% {
		transform: translateY(100%);
	}

	50% {
		opacity: 0;
		transform: translateY(-100%);
	}

	51% {
		opacity: 1;
	}
}

