.animation-scale-x {
	clip-path: inset(0 100% 0 0);
	&.animate__animated {
		animation: qodef-image-fade-in .8s forwards cubic-bezier(.57,.57,0,.97);
	}
}

@keyframes qodef-image-fade-in {
	0% {
		clip-path: inset(0 100% 0 0);
	}
	100% {
		clip-path: inset(0 0 0 0);
	}
}




